*{
  margin: 0;
}
body{
  background-color: rgb(228, 225, 220);
}
.app_header{
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 20px;
}
.app_stats{
  display: flex;
  justify-content: space-between;
 
}
.App{
  display: flex;
  justify-content: space-evenly;
  padding: 20px;
}
@media (max-width: 990px){
  .App{
    flex-direction: column;
  }
}
.app_left{
  flex: 0.9;
}